*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #141b2b;
  color: #fff;
}

.App{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: white;
}

.App__search{
  margin-bottom: 64px;
  display: flex;
  flex-direction:column;
  justify-content: center;
  align-items: center;
}

.App__text{
  margin-bottom: 32px;
  text-align: center;
}

.App__input{
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(-225deg, #ac32e4 0%, #7918f2 48%, #4801ff 100%);
  color: #e2e2e2;
}

.App__input::placeholder{
  color: #e2e2e2;

}

