.coin__container{
    display: flex;
    justify-content: center;
}

.coin__row{
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid#d7d7d7;
    width: 900px;
}

.coin__details {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
} 

.coin__details h1{
    font-size: 16px;
    width: 150px;
}

.coin__details img{
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin__symbol{
    text-transform: uppercase;
}

.coin__data{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin__price{
    width: 110px;
}

.coin__volume{
    width: 200px;
}

.coin__marketcap{
    width: 240px;
}

.coin__percent{
    width: 80px;
}

.red{
    color: #f00606;
}

.green{
    color: #11d811;
}